// main page elements
body {
	font-size: 14px;
	color: #000000;
	font-family: 'Cera Pro';
	font-style: normal;
	font-weight: 300;
	background: white;
	overflow-x: hidden;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden; }

.container {
	max-width: 1200px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1200px) {
		padding: 0 35px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 10px; } }
.mobile {
	display: none; }
.desk_1 {
	display: none; }
.ipad {
	display: none; }
.ipad_1 {
	display: none; }
.flex {
	display: flex; }
.us .slide .dop__block {
	display: flex; }
.dops-mobile {
	display: none; }
.mob, .mob_1 {
	display: none; }
.mobile-slider {
	display: none; }
.btn-absol {
	display: none; }
.btn {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	background: $blue;
	box-shadow: 0px 0px 2px rgba(8, 135, 248, 0.1), 0px 2px 10px rgba(8, 135, 248, 0.2);
	border-radius: 5px;
	text-transform: uppercase;
	color: white;
	font-family: 'Cera Pro';
	font-weight: bold;
	font-size: 14px;
	&:hover {
		background: white;
		color: $blue; }
	&:focus {
		background: #E91C24;
		color: white; }
	&:active {
		background: #E91C24;
		color: white; } }
.btn-absol {
	display: none; }
.btn-top {
	width: 70px;
	height: 70px;
	background: $blue;
	color: white;
	border-radius: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	font-weight: bold;
	font-family: 'BebasNeue';
	font-size: 18px;
	position: fixed;
	bottom: 50px;
	right: 50px;
	z-index: 100000; }
.btn-mail {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	background: inherit;
	border: 1px solid #FFFFFF;
	box-shadow: 0px 0px 2px rgba(8, 135, 248, 0.1), 0px 2px 10px rgba(8, 135, 248, 0.2);
	border-radius: 5px;
	text-transform: uppercase;
	color: white;
	font-family: 'Cera Pro';
	font-weight: bold;
	font-size: 14px;
	&:hover {
		background: white;
		color: $blue; }
	&:focus {
		background: #E91C24;
		color: white; }
	&:active {
		background: #E91C24;
		color: white; } }
